.myModal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5);
    padding: 10px;
}


.active {
    display: flex;
    justify-content: center;
    align-items: center;
}


.myModalContent {
    padding: 25px;
    background: white;
    border-radius: 12px;
    min-width: 250px;
}